/* Обертка для всего экрана */
.full-screen-container {
    position: relative; /* Для позиционирования элементов */
    width: 100%; /* Занимает всю ширину экрана */
    height: 100vh; /* Занимает всю высоту экрана */
    overflow: hidden; /* Скрываем выходящие за пределы экрана элементы */
    user-select: none; /* Запрещаем выделение текста <button class="citation-flag" data-index="7"> */
    /* Градиентный фон с анимацией */
    background: linear-gradient(270deg, #ff9a9e, #fad0c4, #fbc2eb, #a6c1ee, #84fab0, #8fd3f4);
    background-size: 600% 600%; /* Увеличиваем размер градиента для плавного движения */
    animation: gradient-animation 45s ease infinite; /* Анимация переливания */
}

/* Анимация градиента */
@keyframes gradient-animation {
    0% {
        background-position: 0% 50%; /* Начальная позиция */
    }
    50% {
        background-position: 100% 50%; /* Середина анимации */
    }
    100% {
        background-position: 0% 50%; /* Возвращение к начальной позиции */
    }
}

/* Счетчик лопнутых сердец */
.score-counter {
    position: absolute;
    top: 1%; /* Отступ сверху */
    left: 0.5%; /* Отступ слева */
    font-size: 1.5rem;
    font-weight: bold;
    color: #ff1493; /* Ярко-розовый цвет */
    background-color: rgba(255, 248, 251, 0.8); /* Полупрозрачный фон */
    padding: 10px 20px; /* Внутренние отступы */
    border-radius: 10px; /* Закругленные углы */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Тень для объема */
    z-index: 4; /* Размещаем поверх элементов */
    display: flex;
    align-items: center; /* Выравнивание по центру */
    gap: 10px; /* Расстояние между сердечком и текстом */
    cursor: pointer; /* Добавляем указатель курсора */
}

/* Иконка стрелочки */
.arrow-icon {
    font-size: 1rem;
    margin-left: 10px;
}

/* Подменю */
.sub-menu {
    position: absolute;
    top: 60px; /* Отступ от счетчика */
    left: 0;
    background-color: rgba(255, 248, 251, 0.9); /* Полупрозрачный фон */
    padding: 10px 20px;
    border-radius: 10px; /* Закругленные углы */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Тень для объема */
    z-index: 5; /* Размещаем поверх счетчика */
    font-size: 1rem;
    color: #333; /* Темно-серый цвет */
    line-height: 1.6;
    white-space: nowrap; /* Запрещаем перенос строк */
}

/* Кнопка сворачивания контейнера */
.toggle-content-button {
    position: absolute;
    top: 1%; /* Отступ сверху */
    left: 47%; /* Центр по горизонтали */
    font-size: 1rem;
    font-weight: bold;
    color: #ff1493; /* Ярко-розовый цвет */
    background-color: rgba(255, 248, 251, 0.8); /* Полупрозрачный фон */
    padding: 10px 20px; /* Внутренние отступы */
    border-radius: 10px; /* Закругленные углы */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Тень для объема */
    z-index: 4; /* Размещаем поверх элементов */
    cursor: pointer; /* Добавляем указатель курсора */
    transition: transform 0.3s ease; /* Анимация наведения */
}

/* Эффект при наведении на кнопку */
.toggle-content-button:hover {
    transform: scale(1.1);
}

/* Контейнер для основного контента */
.content-container {
    position: absolute; /* Абсолютное позиционирование */
    top: 50%; /* Центрируем по вертикали */
    left: 50%; /* Центрируем по горизонтали */
    transform: translate(-50%, -50%); /* Корректируем центр */
    text-align: center;
    z-index: 3; /* Размещаем поверх элементов */
    background-color: rgba(255, 248, 251, 0.959); /* Полупрозрачный фон */
    padding: 20px; /* Добавляем внутренний отступ */
    border-radius: 10px; /* Закругляем углы */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Добавляем тень для объема */
}

/* Стиль заголовка */
.title {
    font-size: 3em;
    font-weight: bold;
    color: #4e4e4e; /* Темно-серый цвет */
    margin-bottom: 20px;
}

/* Стиль текста */
.text {
    font-size: 1.2rem;
    font-weight: 500;
    color: #333; /* Темно-серый цвет */
    margin: 10px 0;
    line-height: 1.6;
}

/* Выделение важных слов */
.highlight {
    font-weight: bold;
    color: #ff5757; /* Розовый цвет */
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
}

/* Кнопка */
.button {
    background-color: rgba(175, 175, 175, 0.253); /* Прозрачный фон */
    border-radius: 25px;
    border: none;
    cursor: pointer;
    padding: 0; /* Убираем отступы */
    transition: transform 0.3s ease;
}

/* GIF кота внутри кнопки */
.cat-gif {
    width: 100px; /* Размер GIF */
    height: auto;
    display: block; /* Убираем лишние отступы */
}

/* Эффект при наведении на кнопку */
.button:hover {
    transform: scale(1.1);
}

/* Стрелка */
.arrow {
    margin-top: 1px;
}

/* Стиль падающих сердечек */
.falling-heart {
    position: absolute;
    width: 50px; /* Размер сердечка */
    height: auto;
    cursor: pointer; /* Включаем указатель курсора */
    animation: fall linear forwards; /* Анимация падения */
    z-index: 1; /* Размещаем под основным контентом */
    will-change: transform, opacity; /* Оптимизация анимации <button class="citation-flag" data-index="7"> */
}

/* Анимация падения */
@keyframes fall {
    0% {
        top: -50px; /* Начальная позиция сверху */
        opacity: 0; /* Полная прозрачность */
    }
    10% {
        opacity: 1; /* Постепенное появление */
    }
    80% {
        opacity: 1; /* Остается видимым */
    }
    100% {
        top: 100vh; /* Конечная позиция внизу */
        opacity: 0; /* Исчезает */
    }
}

/* Стиль особенных сердечек */
.falling-special-heart {
    position: absolute;
    width: 50px; /* Больший размер для особенных сердечек */
    height: auto;
    cursor: pointer; /* Включаем указатель курсора */
    animation: fall linear forwards; /* Анимация падения */
    z-index: 2; /* Размещаем над обычными сердечками */
    will-change: transform, opacity; /* Оптимизация анимации <button class="citation-flag" data-index="7"> */
    filter: drop-shadow(0 0 5px #ff1493); /* Добавляем эффект свечения */
}

/* Стиль бомб */
.falling-bomb {
    position: absolute;
    width: 80px; /* Размер бомбы */
    height: auto;
    cursor: pointer; /* Включаем указатель курсора */
    animation: fall linear forwards; /* Анимация падения */
    z-index: 3; /* Размещаем над всеми элементами */
    will-change: transform, opacity; /* Оптимизация анимации <button class="citation-flag" data-index="7"> */
    filter: drop-shadow(0 0 5px #ff4500); /* Добавляем оранжевое свечение */
}

/* Стиль сообщения "I love you" */
.love-message {
    position: absolute;
    font-size: 1.2rem;
    font-weight: bold;
    color: #ff1493; /* Ярко-розовый цвет */
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
    animation: fadeInOut 2s ease forwards; /* Анимация появления и исчезновения */
    z-index: 2; /* Размещаем над элементами */
}

/* Анимация появления и исчезновения текста */
@keyframes fadeInOut {
    0% {
        opacity: 0; /* Полная прозрачность */
        transform: scale(0.6); /* Начальный размер */
    }
    20% {
        opacity: 1; /* Полная видимость */
        transform: scale(1); /* Исходный размер */
    }
    80% {
        opacity: 1; /* Остается видимым */
    }
    100% {
        opacity: 0; /* Исчезает */
    }
}

/* Стиль таймера */
.timer {
    position: absolute;
    top: 30%; /* Отступ сверху */
    left: 105%; /* Отступ слева */
    font-size: 1.2rem;
    font-weight: bold;
    color: #ff1493; /* Ярко-розовый цвет */
    background-color: rgba(255, 248, 251, 0.8); /* Полупрозрачный фон */
    padding: 5px 10px; /* Внутренние отступы */
    border-radius: 10px; /* Закругленные углы */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Тень для объема */
    z-index: 4; /* Размещаем поверх элементов */
}